<template>
    <div class="owner-information">
        <div class="container">
            <b-row>
                <b-col cols="12">
                    <div class="heading py-2">
                        <div v-if="isAddListing" class="mb-2">
                            <h1 class="d-flex justify-content-center flex-column align-items-center text-dark font-weight-semi font-size-large text-center mb-2">{{ $t('owner.are_You_Broker') }}</h1>
                            <small>*{{ $t('owner.are_You_Broker_Content') }} <nuxt-link class="font-weight-bold text-uppercase" :to="localePath('/properties/add/7')">{{ dir === 'ltr' ? 'Skip' : 'تخطى' }}</nuxt-link></small>
                            <br>
                            <small>*{{ $t('owner.are_You_Broker_Content_2') }}</small>
                        </div>
                        
                        <h1 v-else class="d-flex justify-content-center flex-column align-items-center text-dark font-weight-semi font-size-large text-center mb-4">
                            <img width="50px" height="50px" :src="require('@/assets/images/owner/info.svg')" alt="Info">
                            <span>{{ $t('owner.heading') }}</span>
                        </h1>
                    </div>
                </b-col>
                <b-col cols="4" class="mx-auto mb-4" v-if="!isCreateMode && !isAddListing">
                    <b-button block variant="outline-danger" @click="unSelectOwner(ownerData.id)">
                        <img :class="[dir === 'ltr' ? 'mr-1' : 'ml-1']" width="20px" height="20px" src="@/assets/images/icons/unselect.svg" alt="UnSelect">
                        {{ $t('owner.unselect_Owner') }}
                    </b-button>
                </b-col>
            </b-row>
            <form>
                <b-row>
                    <b-col cols="12" class="mx-auto">
                        <div class="container--fluid">
                            <b-row :class="['form-row', {'notAddListing': !isAddListing}]">
                                <b-col cols="12" class="input-container">
                                    <label class="font-weight-bold" for="name">{{ $t('owner.ownerName') }}</label>
                                    <b-form-input v-model="ownerData.name" :placeholder="$t('owner.ownerNamePlaceholder')" id="name"></b-form-input>
                                </b-col>
                                <b-col cols="12" class="input-container">
                                    <label class="font-weight-bold" for="adress">{{ $t('owner.address') }}</label>
                                    <b-form-input v-model="ownerData.address" :placeholder="$t('owner.addressPlaceholder')" id="adress"></b-form-input>
                                </b-col>
                                <b-col cols="12" lg="6" class="input-container">
                                    <label class="font-weight-bold" for="email1">{{ $t('owner.email') }}</label>
                                    <b-form-input v-model="ownerData.email" :placeholder="$t('owner.emailPlaceholder')" type="email" id="email1"></b-form-input>
                                </b-col>
                                <b-col cols="12" lg="6" class="input-container">
                                    <label class="font-weight-bold" for="customerCode">{{ $t('owner.customer_Code') }}</label>
                                    <b-form-input v-model="ownerData.ref_id" :placeholder=" dir === 'ltr' ? 'eg: ABC123' : 'مثل: ABC123'" type="email" id="customerCode"></b-form-input>
                                </b-col>
                                <b-col cols="12" lg="6" class="input-container">
                                    <label class="font-weight-bold" for="num1">{{ $t('owner.mobileNumber') }}</label>
                                    <b-form-input v-model="ownerData.first_mobile" :placeholder="$t('owner.mobileNumberPlaceholder')" id="num1"></b-form-input>
                                </b-col>
                                <b-col cols="12" lg="6" class="input-container">
                                    <label class="font-weight-bold" for="num2">{{ $t('owner.anotherMobileNumber') }}</label>
                                    <b-form-input v-model="ownerData.second_mobile" :placeholder="$t('owner.mobileNumberPlaceholder')" id="num2"></b-form-input>
                                </b-col>
                                <b-col cols="12" lg="12" class="input-container">
                                    <label class="font-weight-bold" for="textarea">{{ $t('owner.note') }}</label>
                                    <b-form-textarea
                                    v-model="ownerData.notes"
                                    no-resize
                                    rows="3"
                                    id="textarea"
                                    :placeholder="$t('owner.textArea_placeholder')"
                                    ></b-form-textarea>
                                </b-col>
                            </b-row>
                            <div v-if="!isAddListing" class="footer">
                                <div class="container">
                                    <b-row>
                                        <b-col cols="4">
                                            <b-button size="sm" block class="bg-light text-dark text-center" :to="localePath('/dashboard/my-properties')">
                                                {{ dir === 'ltr' ? 'Back' : 'رجوع'}}
                                            </b-button>
                                        </b-col>
                                        <b-col cols="8" v-if="isCreateMode">
                                            <b-button size="sm" block variant="primary" @click="addOwner()">
                                                {{ dir === 'ltr' ? 'Create New Owner' : 'انشاء مالك جديد' }}
                                            </b-button>
                                        </b-col>
                                        <b-col cols="8" v-else>
                                            <b-button size="sm" block variant="primary" @click="editOwner()" :disabled="active">
                                                {{ dir === 'ltr' ? 'Edit Owner Information' : 'تعديل بيانات المالك' }}
                                            </b-button>
                                        </b-col>
                                    </b-row>
                                </div>
                            </div>
                        </div>
                    </b-col>
                </b-row>
            </form>
        </div>
    </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('UserModule');
const { mapState: propertStates } = createNamespacedHelpers('PropertyModule')
import axios from 'axios';
import $axios from '~/helpers/axios-port';


export default {
    name: "ownerInfo",
    data(){
        return {
            ownerData: {
                name: '',
                address: '',
                email: '',
                ref_id: '',
                first_mobile: '',
                second_mobile: '',
                notes: '',
                property_id: '',
                occupation : 'Broker',
            },
            clonedOwnerData: {},
            active: true,
            ownerId: null,
        }
    },
    computed:{
        ...mapState(['isCreateMode', 'editOwnerId']),
        ...propertStates(['clickedPropToAttachOwner']),
        dir() {
            return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
        },
        computedIsCreateMode(){
            return localStorage.getItem('isCreateMode') === "true";
        },
    },
    props:{
        isAddListing: {
            type: Boolean,
            default: false,
        }
    },
    methods:{
        ...mapActions(['enableCreateMode', 'disableCreateMode']),
        async addOwner(){
            const res = await this.$axios.$post('/user/owners/create', this.ownerData);
            if(res.Status == 200){
                this.$bvToast.toast(`Owner Created and Attached to Owner Successfully.`, {
                    autoHideDelay: 3000,
                    appendToast: false,
                    toaster: 'b-toaster-top-full',
                    variant: 'success',
                });
                this.clearData();
                this.attachOwnerToProp(res.Data.id)
                this.$router.push(this.localePath('/dashboard/my-properties'));
            }
        },
        async editOwner(){
            // For Testing untill getting the user id from property object
            const res = await this.$axios.$post(`/user/owners/${this.ownerId}/update`, this.ownerData);
            if(res.Status == 200){
                this.$bvToast.toast(`Owner Updated successfully.`, {
                    autoHideDelay: 3000,
                    appendToast: false,
                    toaster: 'b-toaster-top-full',
                    variant: 'success',
                });
                this.$router.push(this.localePath('/dashboard/my-properties'));
            }
        },
        async attachOwnerToProp(ownerId){
            const res = await this.$axios.$post(`/user/owners/attach/${this.clickedPropToAttachOwner.id}/owner/${ownerId}`);
            if(res.Status === 200){
                this.$bvToast.toast(`Property Attached To Owner Successfully.`, {
                    autoHideDelay: 3000,
                    appendToast: false,
                    toaster: 'b-toaster-top-full',
                    variant: 'success',
                });
            }
        },
        clearData(){
            this.ownerData.name = '';
            this.ownerData.address = '';
            this.ownerData.email = '';
            this.ownerData.first_mobile = '';
            this.ownerData.second_mobile = '';
            this.ownerData.notes = '';
        },
        async unSelectOwner(ownerId){
            const res = await this.$axios.$post(`/user/owners/deattach/${this.clickedPropToAttachOwner.id}/owner/${ownerId}`);
            if(res.Status === 200){
                this.$bvToast.toast(`Property Deattached From Owner Successfully.`, {
                    autoHideDelay: 3000,
                    appendToast: false,
                    toaster: 'b-toaster-top-full',
                    variant: 'danger',
                });
                this.$router.push(this.localePath('/dashboard/my-properties'));
            }
        }
    },
    async mounted(){
        let currentProperty = JSON.parse(localStorage.getItem('property'));
        if(this.$route.path.indexOf('/properties/add') >= 0 && currentProperty && currentProperty.form6){
            this.ownerData = currentProperty.form6;
        }
        if(JSON.parse(localStorage.getItem('clickedProperty'))){
            this.ownerData.property_id = JSON.parse(localStorage.getItem('clickedProperty')).id || this.clickedPropToAttachOwner.id;
        }
        if(!this.isCreateMode){
            this.ownerId = this.editOwnerId;
            const res = await this.$axios.$get(`/user/owners/${this.editOwnerId}`);
            this.ownerData = res.Data;
        }
        this.active = true;
    },
    watch: {
        ownerData: {
            handler(newVal){
                this.active = false;
            },
            deep: true,
        }
    }
}
</script>

<style lang="scss">
.owner-information {
    background-color: #F7F9FA;
    padding-top: 15px;
    margin-top: 15px;
    margin-bottom: 25px;
    .row {
        // &.form-row.notAddListing {
        //     @media screen and (max-width: 991px) {
        //         margin-bottom: 60px !important;
        //     }
        // }
        .input-container {
            margin-bottom: 10px;
            background-color: #FFF;
            padding: 20px;
            border-radius: 10px;
            margin-bottom: 20px;
        }
    }
    .footer {
        padding: 20px;
        background-color: #FFF !important;
        border-radius: 10px;
        margin-bottom:20px;
        // @media screen and (max-width: 991px) {
        //     position: fixed;
        //     left: 0;
        //     right: 0;
        //     bottom: 0;
        // }
    }
}
</style>